import React, { useState } from 'react';
import styled from 'styled-components';
import ColorTooltip from './ColorTooltip';
import ColorItem from './ColorItem';
const paddingSpace = 4;

const ColorPickerControllerBlock = styled.div`
  position: relative;
  display: inline-block;
  .colors__wrapper {
    margin-left: -4px;
  }
`;
const ColorPickerController = ({ onChange, colors, children, ...rest }) => {
  const [values, setValues] = useState({ active: false, selected: null });
  const { active, selected } = values;
  const onClick = () => {
    setValues({
      ...values,
      active: !active,
    });
  };
  const onClickColorItem = (selectedColor) => {
    setValues(selectedColor);
    if (onChange) return onChange(selectedColor);
  };
  return (
    <ColorPickerControllerBlock>
      <div
        onClick={onClick}
        style={{
          backgroundColor: selected ? selected : '#E3D4FF',
          width: '20px',
          height: '20px',
          borderRadius: '50%',
        }}
      ></div>

      {active && (
        <ColorTooltip>
          <div className="clearfix colors__wrapper">
            {colors.map((color, key) => (
              <ColorItem
                key={key}
                paddingSpace={paddingSpace}
                onClick={onClickColorItem}
                color={color}
              />
            ))}
          </div>
        </ColorTooltip>
      )}
    </ColorPickerControllerBlock>
  );
};

export default ColorPickerController;
