import React, { useRef } from 'react';
import styled from 'styled-components';
import useOutsideDetector from '../hooks/useOutsideDetector';

const ColorTooltipBlock = styled.div`
  position: absolute;
  box-shadow: 4px 4px 1px -2px rgb(0 0 0 / 10%);
  left: 100%;
  top: 100%;
  padding-top: 4px;
  padding-bottom: 4px;
  display: block;
  width: 126px;
  padding: 5px 5px;
  border-radius: 12px;
`;
const ColorTooltip = ({ children, ...rest }) => {
  const ref = useRef(null);
  useOutsideDetector(ref);
  return <ColorTooltipBlock ref={ref}>{children}</ColorTooltipBlock>;
};

export default ColorTooltip;
