import React from 'react';
import styled from 'styled-components';
import UpdangCalendar from './UpdangCalendar';
import MyAndRecomendButton from './Buttons/MyAndRecomendButton';
import ReactDOM from 'react-dom';
import PrependPortal from './helper/PrependPortal';

class CustomUpdangCalendar extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isDomRendered: false };
    this.customButtonTargetName = '.fc-daygrid.fc-dayGridMonth-view.fc-view';
  }
  componentDidMount() {
    !this.state.isDomRendered &&
      this.setState({ ...this.state, isDomRendered: true });
  }
  render() {
    const { events } = this.props;
    const { isDomRendered } = this.state;
    const container = document.querySelector(this.customButtonTargetName);
    return (
      <React.Fragment>
        <UpdangCalendar events={events} />
        {isDomRendered && (
          <PrependPortal
            container={container}
            Component={
              <MyAndRecomendButton
                onClickCalendarChange={this.props.onClickCalendarChange}
                selected={this.props.calendarType}
              />
            }
          />
        )}
      </React.Fragment>
    );
  }
}

export default CustomUpdangCalendar;
