import React from 'react';
import styled from 'styled-components';

const ScrollDirectionChangerBlock = styled.div`
  transform-origin: left top;
  transform: rotate(-90deg) translate(-100%, 0);
  width: 100vh;
  height: 100vw;
  overflow-x: hidden;
  overflow-y: scroll;
  float: left;
  /* 
  transform-origin: top left;
  transform: rotate(-90deg) translate(-100%, 0);
  width: 100vh;
  height: 100vw;
  overflow-y: scroll;
  overflow-x: hidden; */

  // -ms-overflow-style: none; /* IE and Edge */
  // scrollbar-width: none; /* Firefox */
  /* 
  .YTOX__wrapper {
    transform-origin: top left;
    transform: rotate(-90deg) translate(-100%, 0);
    width: ${(props) => (props.height ? props.height + 'px' : '100vh')};
    height: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
    -ms-overflow-style: none; 
    scrollbar-width: none; 
  } 
  */
  .YTOX__wrapper::-webkit-scrollbar {
    // display: none; /* Chrome, Safari, Opera*/
  }
`;
const ScrollDirectionChanger = React.forwardRef(
  ({ onScroll, height, children, ...rest }, ref) => {
    console.log(['ref scroll direction changer,'], ref);
    return (
      <ScrollDirectionChangerBlock
        onScroll={onScroll}
        {...rest}
        height={height}
        ref={ref}
      >
        {children}
      </ScrollDirectionChangerBlock>
    );
  }
);

export default ScrollDirectionChanger;
