import React, { useEffect, useState, useRef } from 'react';
import styled from 'styled-components';
import CarouselView from './CarouselView';
import { RecommendedProduct } from '../../Cards';
import helper from '../../helper';
import ScrollDirectionChanger from './ScrollDirectionChanger';
import useScrollTrackMover from '../hooks/useScrollTrackMover';
import ScrollTrackMover from './ScrollTrackMover';
const { pixelToRem } = helper;

const LIMIT = 5;
const CarouselControllerBlock = styled.div`
  /* 
  transform: rotate(90deg) translate(0, -100%);
  transform-origin: top left;
  float: left;
  display: flex;
 */
  .trailor__wrapper {
  }
  .item__wrapper {
    float: left;
  }
  .horizontal-scroll {
  }
  .caroucel__item-wrapper {
    padding-left: ${pixelToRem(20)};
    transform: rotate(90deg);
  }
  .caroucel__item {
    width: ${pixelToRem(174)};
  }
  .updown {
    animation: updown 8s linear infinite;
  }

  .downup {
    animation: downup 8s linear infinite;
  }
`;
const paddingSize = 20;

const CarouselController = ({ list, children, ...rest }) => {
  const [products, setProducts] = useState([]);
  const scrollRef = useRef();

  // useScrollTrackMover(scrollRef);
  const onScroll = () => {
    const scrollContainer = scrollRef.current;
    const listContainer = scrollContainer.firstElementChild;
    const appendTo = listContainer.childNodes;
    appendTo.forEach((el, index) => {
      const object = el;
      const objectOuterWidth = object.offsetWidth;
      const offsetLeftPos = object.getBoundingClientRect().x; //    . // offsetLeft 
      const itemDisappearValue = -(objectOuterWidth + paddingSize); // 214 =  194 + 20
      const scrollWindowWidth =
        objectOuterWidth * (appendTo.length - 1) - paddingSize;

      if (offsetLeftPos <= itemDisappearValue) {
        listContainer.append(object);
      } else if (offsetLeftPos >= scrollWindowWidth) {
        listContainer.prepend(object);
      }
      if (scrollContainer.scrollTop === 0) {
        scrollContainer.scrollTop = paddingSize;
      }
    });
  };
  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = paddingSize;
      scrollRef.current.addEventListener('scroll', onScroll);
    }

    return () => {
      scrollRef.current.removeEventListener('scroll', onScroll);
    };
  }, [scrollRef]);

  console.log(['scrollRef'], scrollRef);
  return (
    <React.Fragment>
      <ScrollDirectionChanger
        id="scroll_container"
        height={250}
        ref={scrollRef}
      >
        <CarouselControllerBlock id="items__wrapper">
          {list.map((item, key) => {
            return (
              <div key={key} className="caroucel__item-wrapper">
                <RecommendedProduct
                  className="caroucel__item"
                  label={item.infoCate}
                  labelColor={item.infoColor}
                  title={item.infoSubject}
                  agency={item.infoLocal}
                  description={item.infoDesc}
                  date={`${item.infoStartDt}-${
                    item.infoEndDt == '' ? '' : item.infoEndDt
                  }`}
                  logo={item.infoLogoImg}
                />
              </div>
            );
          })}
        </CarouselControllerBlock>
      </ScrollDirectionChanger>
    </React.Fragment>
  );
};

export default CarouselController;
